@import url("https://fonts.googleapis.com/css2?family=EB+Garamond&family=Oswald:wght@200;300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=EB+Garamond&family=Oswald&display=swap");

.App {
  // background: url("../public/files/window-background.jpg");
  background-color: #f0ede2;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  height: 100vh;
  align-items: center;
}

h3 {
  font-weight: 400;
}

a:hover {
  color: #2c1d0d;
}

a {
  color: #986b01;
  font-family: "EB Garamond", serif;
  text-decoration: none;
  padding-right: 20px;
  transition: color 0.1s ease-out 20ms;
}

.Back-to-home {
  width: 100%;
  padding-left: 41px;
  padding-top: 24px;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 10px;
  grid-auto-rows: minmax(30px, auto);
  padding-top: 20px;
  width: 100%;

  .subtitle {
    font-size: 20px;
    margin: 0;
    grid-row: 2;
    grid-column: 3/7;
    font-family: "EB Garamond", serif;
  }

  .title--name {
    font-family: "Oswald", sans-serif;
    font-size: 40px;
    letter-spacing: 0.2em;
    grid-column: 3/7;
    grid-row: 1;
    margin-top: 0;
  }

  .title--exp {
    grid-column: 2/5;
    grid-row: 3;
    text-align: center;
    font-family: "Oswald", sans-serif;
    letter-spacing: 0.2em;
    margin-top: 0;
    grid-template-columns: repeat(3, 1fr);
    display: grid;

    .exp--left {
      grid-column: 1;
    }

    .exp--right {
      grid-column: 2/4;
    }
  }
  .paragraph {
    text-align: center;
    font-family: "EB Garamond", serif;
    font-size: 17px;
    display: flex;
    text-align: left;
    padding-top: 10px;
    margin: 0;
    grid-row: 3;
    grid-column: 3/6;
  }
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
  letter-spacing: 0.2em;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
